import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'
import NotFound from '@/views/notfound.vue'

const Company = () => import('./views/company.vue')
const Apply = () => import('./views/apply.vue')
const ApplyRecord = () => import('./views/applyRecord.vue')
const Union = () => import('./views/union.vue')
Vue.use(Router)

export default new Router({
  routes: [{
    path: '',
    name: 'Index',
    component: Index,
    meta: {
      pageTitle: '选择项目'
    }
  }, {
    path: '/union/:serviceType',
    component: Union,
    name: 'union'
  }, {
    path: '/company/:serviceType',
    component: Company,
    name: 'company'
  }, {
    path: '/apply',
    component: Apply,
    name: 'apply',
    meta: {
      pageTitle: '申请服务'
    }
  }, {
    path: '/apply-record',
    component: ApplyRecord,
    name: 'applyRecord',
    meta: {
      pageTitle: '申请记录'
    }
  }, {
    path: '/error',
    component: NotFound,
    name: 'error'
  }, {
    path: '*',
    component: NotFound,
    name: 'notfound'
  }]
})
