import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Axios from '@/common/request.js'
import ApiConfig from '@/apiConfig.js'
import store from './store.js'
import {Cell, CellGroup, Toast, Popup, List, Field, Picker} from 'vant'
import * as utils from '@/common/utils.js'

Vue.use(Cell).use(CellGroup).use(Toast).use(Popup).use(List).use(Field).use(Picker)
Vue.use(ApiConfig)

router.beforeEach((to, from, next) => {
  if (!!to.meta.pageTitle && typeof to.meta.pageTitle !== undefined) {
    utils.setPageTitle(to.meta.pageTitle)
  }
  next()
})
Vue.prototype.axios = Axios
Vue.prototype.ApiConfig = ApiConfig
Vue.config.productionTip = false
document.addEventListener('touchstart', () => {
}, false)

router.beforeEach((to, from, next) => {
  if (!store.state.userInfo) {
    // 用户权限
    Axios.post(Vue.prototype.apiConfig.getUserInfo).then(res => {
      store.dispatch('UPDATE_USERINFO', res.d)
      next()
    })
  } else {
    next()
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
