<template>
  <div class="page">
    <div class="content">
      <van-cell-group v-if="tag==1">
        <van-cell title="企业办公" url="#/company/1" is-link/>
        <van-cell title="企业送水" url="#/company/2" is-link/>
      </van-cell-group>
      <van-cell-group v-if="tag==2">
        <van-cell title="IT服务" url="#/union/1" is-link/>
        <van-cell title="法律服务" url="#/union/2" is-link/>
      </van-cell-group>
    </div>
    <div class="tc p15">
      <a class="theme-color" :href="'#/apply-record?zone_id=' + zone_id">申请记录</a>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'Index',
    data() {
      return {
        tag: null,
        zone_id: null,
        userInfo: {}
      }
    },
    created() {
      this.userInfo = JSON.parse(JSON.stringify(this.$store.state.userInfo))
      this.initPage()
    },
    methods: {
      initPage() {
        let tag = utils.getQueryString(window.location.href, 'tag')
        if (this.userInfo) {
          this.zone_id = this.userInfo.zid
          if (!tag || !this.zone_id) {
            this.$router.push({
              name: 'error'
            })
          } else {
            this.tag = tag
          }
        }
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  #app {
    .van-cell {
      font-size: rem(16px);
      padding: rem(15px);
      input {
        line-height: rem(24px);
        font-size: rem(16px);
      }
      textarea {
        min-height: rem(50px);
        line-height: 1.6;
      }
    }
    .van-field .van-cell__title {
      font-size: rem(16px);
      max-width: rem(90px);
    }
  }
</style>
