import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: ''
  },
  mutations: {
    UPDATE_USERINFO(state, data) {
      state.userInfo = data
    }
  },
  actions: {
    UPDATE_USERINFO({commit}, data) {
      commit('UPDATE_USERINFO', data)
    }
  }
})
